.right_image_project {
    position:relative;
    display: flex;
    width:100%;
    height:100%;
}

.fullscreenFill {
    height: 100vh;
    width: 100%;
}

.right .fullscreenFill:nth-child(1){
    background-color: #ffc09f;
}
.right .fullscreenFill:nth-child(2){
    background-color: #fcf5c7;
}
.right .fullscreenFill:nth-child(3){
    background-color: #a0ced9;
}
.right .fullscreenFill:nth-child(4){
    background-color: #adf7b6;
}
